








import Vue from 'vue';
import {Component,Prop} from 'vue-property-decorator';

@Component
export default class DayType extends Vue{
@Prop(String) value!: string;
  selectedDayType(type: string) {
    if (type !== '1' && type !== '2' && type !== '3') {
      throw new Error('');
    }
    this.$emit('update:value',type)
  }
}
