




























import Types from '@/components/Types.vue';
import Vue from 'vue';
import {Component, Watch} from 'vue-property-decorator';
import store from '@/store/index2';
import NoData from '@/views/NoData.vue';


@Component({
             components: {NoData, Types},
           })
export default class Labels extends Vue {
  type: string = '-';
  tags: Tag[] = store.fetchTag();
  tagsType = this.tags.filter(tag => tag.type === this.type);
  visible: boolean = false;
  typeName = '';

  created(){
    const routeParams = this.$route.params.addBeforeType;
    if(routeParams !== undefined){
      this.type = routeParams;
    }
  }

  @Watch('type')
  onRecordTypeChange(value: string){
    this.tagsType = this.tags.filter(tag => tag.type === value);
    value === '-'?this.typeName ='' : this.typeName = '';
  }
  deleteTag(id: number){
    store.deleteTag(id);
    this.tags = store.fetchTag();
    this.tagsType = this.tags.filter(tag => tag.type === this.type)
    return;
  }
}
